.site-menu {
  padding: 0;
  font-family: $font-heading;
  line-height: 1;
  text-transform: uppercase;
  list-style: none;

  @media (max-width: $breakpoint-small-down) {
    border-top: 1px solid $color-gray-150;
    font-size: 20px;
    font-weight: 500;
    text-align: center;
  }

  @media (min-width:$breakpoint-medium) {
    display: flex;
    justify-content: flex-end;
    font-size: 18px;
  }
}

.site-menu__item {
  @media (max-width: $breakpoint-small-down) {
    border-bottom: 1px solid $color-gray-150;
  }
}

.site-menu__item--active {
  @media (min-width: $breakpoint-medium) {
    position: relative;

    &::after {
      content: "";
      position: absolute;
      bottom: 4px;
      right: 17px;
      left: 17px;
      height: 2px;
      background-color: $color-primary-500;
    }
  }
}

