.site-menu__link {
  display: block;
  padding: 23px 22px 22px;
  color: $color-black;
  text-decoration: none;

  &:link:hover {
    opacity: 0.6;
  }

  &:link:active {
    opacity: 0.3;
  }

  @media (min-width: $breakpoint-medium) {
    padding: 17px;
  }
}

